import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    hotelList: [],
    currentHotel: {
      num: 0,
      name: '',
      fullInfo: {
        rooms: [],
        items: []
      }
    },
    serverURL: '', //       API
    username: '', //  
    links: [ //   sideMenu
      { name: '', link: 'config' },
      { name: '  ', link: 'work' }
    ],
    tableList: [], //      
    currentTable: { //      
      num: null, //   
      name: '', //   
      curPage: 1, //    
      maxPage: 0, //     
      perPage: 5, //     
      columns: [], //     -   
      rows: [], //      
      length: 0 //    
    },
    statusBar: { //     
      text: 'statusBar', //  
      hidden: false // /
    },
    currentUser: 0, //   - 
    newRow: {}, //       
    imageSrc: '' //       
  },
  getters: {
    getTableList: (state) => {
      return state.tableList
    },
    getTableRows: (state) => {
      return state.tableRows
    },
    getCurrentTableNum: (state) => { // 
      return state.currentTable.num
    },
    getCurrentTable: (state) => {
      return state.currentTable
    },
    getCurrentTableRows: (state) => {
      return state.currentTable.rows
    },
    getNewRow: (state) => {
      return state.newRow[state.currentTable.name]
    }
  },
  mutations,
  actions
})
